import React from 'react';
import { Helmet } from 'react-helmet';
import ProjectsComponent from './projectsComponent';


const BhajanLyricsPage = (): React.ReactElement => {
  const url = new URL(window.location.href);
  const iOSAppId = 'id1553323738';
  const iOSAppLocationURI = `https://apps.apple.com/us/app/bhajanlyrics/${iOSAppId}`
  const deepLinkLocation = `bhajanlyrics://bhajans${url.search}`;
  const androidAppId = 'com.developerrsquared.bhajanlyrics';
  const appSharePath = 'projects/bhajan-lyrics/app';
  const appName = "Bhajan Lyrics";
  const pagePath = "https://www.rikhilrai.com/projects/bhajan-lyrics/";
  const pagePathWithSearch = `${pagePath}${url.search}`;

  return (
    <div>
      <Helmet
        script={[{
          type: 'text/javascript',
          innerHTML: `
function redirect() {
  var url = new URL(window.location.href);
  var userAgent = navigator.userAgent || navigator.vendor || window.opera;
  var ios = /iPad|iPhone|iPod/.test(userAgent) && !window.MSStream;
  var language = /fr|FR/.test(navigator.language) ? 'fr' : 'en';
  if (ios) {
    window.location = 'bhajanlyrics://bhajans' + url.search;
    window.setTimeout(() => {
      window.location.replace('${iOSAppLocationURI}');
    }, 1000)
    return
  }
  var android = /android/i.test(userAgent);
  if (android) {
    window.location = 'bhajanlyrics://bhajans' + url.search;
    window.setTimeout(() => {
      window.location.replace('https://play.google.com/store/apps/details?id=${androidAppId}&hl=' + language);
    }, 25)
    return
  }
  window.location.replace('https://www.rikhilrai.com/projects/bhajan-lyrics');
};
if (window.location.href.includes('${appSharePath}')) {
  redirect()
}
`
        }]}
      >
        <title>{appName}</title>
        <meta name="description" content="Application for Bhajan Lyrics. Your Bhajan Lyrics pocket diary." />

        <link rel="icon" type="image/png" sizes="32x32" href="https://www.rikhilrai.com/bhajan-lyrics-32x32.png" />
        <link rel="icon" type="image/png" sizes="16x16" href="https://www.rikhilrai.com/bhajan-lyrics-16x16.png" />

        {/* Facebook Meta Tags */}
        <meta property="og:site_name" content={`${appName} · Rikhil Raithatha`} />
        <meta property="og:title" content={`${appName} · Rikhil Raithatha`} />
        <meta property="og:description" content={`Application for Bhajan Lyrics. Your Bhajan Lyrics pocket diary.`} />
        <meta property="og:url" content={`https://www.rikhilrai.com${url.pathname}${url.search}`} />
        <meta property="og:type" content="website" />
        <meta property="og:image" content="https://www.rikhilrai.com/bhajan-lyrics-1200x628.png" />
        <meta property="og:image:width" content="1200" />
        <meta property="og:image:height" content="628" />
        <meta property="og:image:type" content="image/png" />

        {/* Twitter Meta Tags */}
        <meta property="twitter:domain" content="rikhilrai.com" />
        <meta property="twitter:url" content={`https://www.rikhilrai.com${url.pathname}${url.search}`} />
        <meta name="twitter:card" content="summary_large_image" />
        <meta name="twitter:site" content="@rikhilrai" />
        <meta name="twitter:title" content="Rikhil Raithatha" />
        <meta name="twitter:description" content="Application for Bhajan Lyrics. Your Bhajan Lyrics pocket diary." />
        <meta name="twitter:image" content="https://www.rikhilrai.com/bhajan-lyrics-1200x628.png" />
        <meta name="twitter:image:alt" content="Bhajan Lyrics banner" />

        <meta property="al:ios:url" content={deepLinkLocation} />
        <meta property="al:ios:app_store_id" content={iOSAppId} />
        <meta property="al:ios:app_name" content={appName} />

        <meta property="al:android:url" content={deepLinkLocation} />
        <meta property="al:android:package" content={androidAppId} />
        <meta property="al:android:app_name" content={appName} />

        <meta property="al:web:url" content={pagePathWithSearch} />
        <meta property="al:web:should_fallback" content="false" />
      </Helmet>
      <ProjectsComponent />
    </div>
  )
}

export default BhajanLyricsPage;
