import * as React from 'react';
import { Route, Switch } from 'react-router-dom';

import HomePage from './pages/homePage';
import NotFoundPage from './pages/404Page';
import ProjectsPage from './pages/projectsPage';
import ContactsPage from './pages/contactPage';
import AlankaarsPrivacyPolicy from './pages/alankaarsPrivacyPolicy';
import { usePageTracking } from './util/usePageTracking';
import BhajanLyricsPrivacyPolicy from './pages/bhajanLyricsPrivacyPolicy';
import AlankaarsPage from './pages/alankaarsPage';
import YoutubePage from './pages/youtubePage';
import BhajanLyricsPage from './pages/bhajanLyricsPage';


const App = (): React.ReactElement => {
  usePageTracking();

  // NOTE(rikhil): /app routes are here for og tags
  return (
    <Switch>
      <Route path={process.env.PUBLIC_URL + '/'} exact={true} component={HomePage} />
      <Route path={process.env.PUBLIC_URL + '/contact'} exact={true} component={ContactsPage} />
      <Route path={process.env.PUBLIC_URL + '/projects/alankaars/app'} exact={true} component={AlankaarsPage} />
      <Route path={process.env.PUBLIC_URL + '/projects/alankaars/app/'} exact={true} component={AlankaarsPage} />
      <Route path={process.env.PUBLIC_URL + '/projects/alankaars/privacy-policy'} exact={true} component={AlankaarsPrivacyPolicy} />
      <Route path={process.env.PUBLIC_URL + '/projects/alankaars'} component={AlankaarsPage} />
      <Route path={process.env.PUBLIC_URL + '/projects/bhajan-lyrics/app'} exact={true} component={BhajanLyricsPage} />
      <Route path={process.env.PUBLIC_URL + '/projects/bhajan-lyrics/app/'} exact={true} component={BhajanLyricsPage} />
      <Route path={process.env.PUBLIC_URL + '/projects/bhajan-lyrics/privacy-policy'} exact={true} component={BhajanLyricsPrivacyPolicy} />
      <Route path={process.env.PUBLIC_URL + '/projects/bhajan-lyrics'} component={BhajanLyricsPage} />
      <Route path={process.env.PUBLIC_URL + '/projects'} exact={true} component={ProjectsPage} />
      <Route path={process.env.PUBLIC_URL + '/youtube-player'} exact={true} component={YoutubePage} />
      <Route path='*' component={NotFoundPage} />
    </Switch>
  )
};

export default App;
